.mobilenav.on {
    transition: opacity ease-out 0.3s, -webkit-transform ease-in-out 0.4s;
    transition: transform ease-in-out 0.4s, opacity ease-out 0.3s;
    transition: transform ease-in-out 0.4s, opacity ease-out 0.3s, -webkit-transform ease-in-out 0.4s;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.mobilenav.off {
    transition: opacity ease-out 0.3s, -webkit-transform ease-in-out 0.4s;
    transition: transform ease-in-out 0.4s, opacity ease-out 0.3s;
    transition: transform ease-in-out 0.4s, opacity ease-out 0.3s, -webkit-transform ease-in-out 0.4s;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

